.formControl {
    margin-bottom: 10px;
}

.formControl textarea {
    padding: 5px;
    border: 1px solid #ccc;
}

.formControl.error textarea {
    border: 2px solid red;
}

.formControl.error span {
    color: red;
    font-size: 12px;
}
.formsummaryerror {
    border: 1px solid red;
    padding: 5px;
    color: red ;
}